import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { CustomInput } from 'reactstrap';
import { EditForm, EditViewProps, ERROR, findByExample, showToast, WindowComponent } from 'summer';
import { CLASS_NAME, UsuarioConfig } from '../../../../models/dtos/ger/usuarioConfig';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface ConfigUsuarioEditViewProps extends EditViewProps<UsuarioConfig> {}

const ConfigUsuarioEditView: FC<ConfigUsuarioEditViewProps & RouteComponentProps> = props => {
  const { reset, ...formProps } = useForm({ mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(() => {
    const usuarioConfigExample: UsuarioConfig = {
      usuario: {
        id: globalReducer.globalParameter.usuario.id,
      },
    };
    findByExample(usuarioConfigExample, CLASS_NAME, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: result => {
        reset(result);
      },
    });
  }, [globalReducer.globalParameter.usuario.id, reset]);

  return (
    <WindowComponent title="Preferências de Usuário" wikiId={3223}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <CustomInput
          type="switch"
          id="mesCorrenteConsultaBranco"
          name="mesCorrenteConsultaBranco"
          label="Utilizar o mês corrente como parâmetro para consultas em branco"
          innerRef={formProps.register}
        />
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigUsuarioEditView;
